<template>
    <div class="nx-container-full">
        <div class="nx-container-full__header" ref="header">
            <slot name="header"></slot>
        </div>
        <div class="nx-container-full__body">
            <slot></slot>
        </div>
        <div class="nx-container-full__footer" v-if="$slots.footer" ref="footer">
            <slot name="footer"></slot>
        </div>
    </div>
</template>
<script>
export default {
    name: 'nx-container-full'
}
</script>







